import React, { Component } from 'react'
import TreeSelect from "antd/lib/tree-select"
// import "antd/lib/tree-select/style/css"

console.warn(TreeSelect)
const wait = new Promise(r => {
    setTimeout(r, 5 * 1000)
})

export default class Tse extends Component {
    state = {
        treeData: [],
        selected: ['11', '111', '112'],
        value: [],
    }
    onChange = (value) => {
        console.log(arguments);
        this.setState({ value });
    }
    async componentWillMount() {
        await wait
        let res = await fetch('http://localhost:8888/treeData')
        let json = await res.json()
        console.warn(json.deps)
        this.setState({
            treeData: json.deps,
        }, () => {
            this.setState({
                value: json.selected,
            })
        })
    }
    render() {
        console.warn(this.state.selected)
        return (
            <TreeSelect
                showSearch
                multiple
                onChange={this.onChange}
                value={this.state.value}
                defaultValue={this.state.selected}
                style={{ width: 300 }}
                treeCheckable
                treeNodeFilterProp="label"
                dropdownMatchSelectWidth
                showCheckedStrategy={TreeSelect.SHOW_PARENT}
                dropdownStyle={{ maxHeight: 400, overflow: 'auto', width: 300 }}
                placeholder="Please select"
                treeDefaultExpandAll
                treeData={this.state.treeData}
            />
        )
    }
}


